.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.button{
  background-color: black;
  color: white;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 12px;
  border-radius: 15px;
  margin-left: 10px;
  width: 150px;
  letter-spacing: 0.5px;
}
.popular{
  background-color: white;
  padding: 10px;
  margin-left: 5px;
  border: collapse;
}